import dels from '@/static/mbcImg/my/dels.png';
import business from '@/components/common/RHX/business/business';
import Image2 from '@/static/mbcImg/my/Image2.png';
import Image3 from '@/static/mbcImg/my/Image3.png';
import wei from '@/static/mbcImg/my/15.png';
import xuan from '@/static/mbcImg/my/16.png';
import c from '@/static/mbcImg/my/c.png';
export default {
	components: {
		business,
	},
	data() {
		return {
			dels:dels,
			add:false,//遮罩显示隐藏
			Image2:Image2,
			Image3:Image3,
			ding:false,//立即定制显示隐藏
			addMaskAb:false,//立即定制背景，字体颜色
			addMasklians:false,//添加链接背景，字体颜色
			lian:false,//链接显示隐藏
			imges:0,//复选框
			phone:'',//电话
			num:'1',
			nums:'1',
			wei:wei,
			xuan:xuan,
			c:c,
			addtos:'添加按钮',
			addtos2:'添加按钮',
			addname:'',//按钮名称
			Website:'',//链接地址
			list:[
				{
					img:c,
					name:'北欧布艺沙发定制',
					content:'上层天然异色羽绒，柔韧舒适，下层出口美国无公害压缩棉阿斯加德就喀什就看的安居客收到货奥科吉说的和爱神的箭客户萨',
				},
				
			]
		};
	},
	created() {},
	methods: {
		image(e){//复选框事件
			this.imges=e;
		},
		img(){
			if(this.addtos==this.addname){
				this.addname='';
				this.Website='';
				this.addtos='添加按钮';
			}else if(this.addtos!=='添加按钮'){
				this.addtos='添加按钮';
			}
		},
		imgs(){
			if(this.addtos2==this.addname){
				this.addname='';
				this.Website='';
				this.addtos2='添加按钮';
			}else if(this.addtos2!=='添加按钮'){
				this.addtos2='添加按钮';
			}
		},
		addto(){
			this.add=true;
			if(this.addtos === '立即定制'){
				this.addMaskAb=true;
				this.ding=true;
				this.num = '2';
			}else if(this.addtos !== '立即定制' && this.addtos !== '添加按钮'){
				this.lian=true;
				this.nums='2';
				this.addMasklians=true;
			}
		},
		addtoto(){
			this.add=true;
			if(this.addtos2 === '立即定制'){
				this.addMaskAb=true;
				this.ding=true;
				this.num = '2';
			}else if(this.addtos2 !== '立即定制' && this.addtos2 !== '添加按钮'){
				this.lian=true;
				this.nums='2';
				this.addMasklians=true;
			}
		},
		// 点击上面的遮罩
		Mask(){
			// 立即定制
			this.add=false;
			this.ding=false;
			this.num = '1';
			this.addMaskAb=false;
			// 添加链接
			this.lian=false;
			this.nums='1';
			this.addMasklians=false;
			if(this.Website===''|| this.addname===''){
				this.addtos2='添加按钮';
				this.Website='';
				this.addname='';
			}else{
				if(this.addname === ''){
					
				}else{
					if(this.addtos!=='添加按钮' && this.addtos!=='立即定制'){
						this.addtos=this.addname;
					}else if(this.addtos2 ==='添加按钮' || this.addtos2!=='立即定制'){
						this.addtos2=this.addname;
					}
				}
			}
			this.$emit('addtoTap',{'addtap':this.addname,'link':this.Website,'phone':this.phone})
		},
		child(){},
		addMaskAc(){
			if(this.num === '1'){
				this.ding=true;
				this.num = '2';
				this.addMaskAb=true;
				if(this.addtos==='添加按钮' || this.addtos==='立即定制'){
					this.addtos='立即定制';
				}else if(this.addtos2 ==='添加按钮'){
					this.addtos2='立即定制'
				}
				// 添加链接
				this.lian=false;
				this.nums='1';
				this.addMasklians=false;
			}else{
				this.ding=false;
				this.num = '1';
				this.addtos='添加按钮'
				this.addMaskAb=false;
			}
			
		},
		addMasklian(){//添加事件
			if(this.nums === '1'){
				this.lian=true;
				this.nums='2';
				this.addMasklians=true;
				// if(this.addtos==='添加按钮' || this.addtos==='添加链接'){
				// 	this.addtos='添加链接';
				// }else if(this.addtos2 ==='添加按钮') {
				// 	this.addtos2='添加链接'
				// }
				// 立即定制
				this.ding=false;
				this.num = '1';
				this.addMaskAb=false;
			}else{
				this.lian=false;
				this.nums='1';
				this.addMasklians=false;
				this.Website='';
				this.addname='';
				this.addtos2 ='添加按钮'
			}
			
		},
		bottomPhone:function(asd){
			console.log(asd)
			this.phone=asd;
		}
	}
};
